const root = $('html, body');

$(document).ready(function() {
    $("a[href^='#']").click(function (e) {
        e.preventDefault();

        let hash = this.hash;
        let target = $(hash);

        $('html, body').stop().animate({
            'scrollTop': target.offset().top
            }, 1250, 'easeOutQuart', function () {
                window.location.hash = hash;
            });
    })
});